<script>
export default {
  data() {
    return ({
      show: false,
      videoList: [
        {
          src: require('../assets/img/home/swiper1.png'),
          like: false,
          msg: `国家安全教育的内容，主要包括政治安全、国土安全、军事安全、经济安全、文化安全、社会安全、科技安全、网络安全、生态安全、资源安全、核安全、海外利益安全以及太空、深海、极地、生物等不断拓展的新型领域安全。
安全，是人类的本能欲望。中国人一向以安心、安身为基本人生观，并以居安思危的态度促其实现。
因而视安全为教育的一个重要环节。由于社会的进步，人类生活方式愈趋复杂，可能危害身体生命安全的情况随之增加。因此，国际君友会呼吁各级学校加强实施安全教育，并增设课程，而与各有关课程及课外活动配合实施。`,
          collect: false,
        }, {
          src: require('../assets/img/home/swiper2.jpg'),
          like: false,
          msg: '国家安全是安邦定国的重要基石，维护国家安全是全国各族人民根本利益所在。坚持总体国家安全观，是习近平新时代中国特色社会主义思想的重要组成部分，是中国国家安全理论的最新成果，是维护国家安全的行动纲领和科学指南，对决胜全面建成小康社会、加快推进社会主义现代化、实现中华民族伟大复兴的中国梦具有深远的重要意义。',
          collect: false,
        }, {
          src: require('../assets/img/home/swiper3.jpg'),
          like: false,
          msg: `•安全+行动=平安。<br>
•安全伴我校园,快乐圆梦今朝。<br>
•安全伴我心,快乐伴我行。<br>
•安全从心开始,共创和谐校园。<br>`,
          collect: false,
        }, {
          src: require('../assets/img/home/swiper4.jpg'),
          like: false,
          msg: `1 、爱惜生命，请勿戏水。　　<Br>2、泳池畅泳乐缤纷，照顾儿童要留心。<Br>3、泳池畅泳要小心，父母同行倍安心。　　<Br>4、泳池防范意外生，亲子畅泳要留神。　　<Br>5、泳池设施齐玩乐，成人不在危险来。　　<Br>6、强化教育，增强青少年儿童的安全意识。　　<Br>7、加强领导，全面落实预防溺水安全措施。　　<Br>8、珍爱生命，关爱为先，切实做好防溺水工作。　　<Br>9、安全第一，预防为主。　　<Br>10、平安伴我在校园，人人时时保平安。`,
          collect: false,
        },
      ],
      flag: false,
      msg: ''
    })
  },
  methods: {
    showMsg(item) {
      this.show = !this.show
      this.msg = item.msg
    },
    change(index) {
      this.flag = false
      this.play(index)
    },
    play(index) {
      let video = document.getElementsByTagName('video')
      for (let i = 0; i < video.length; i++) {
        video[i].pause()
      }
      this.flag = !this.flag
      if (this.flag) {
        video[index].play()
      } else {
        video[index].pause()
      }
    },
  }
}
</script>

