          content:`生活中的环保知识:<br>

　　一、家庭如何节水<br>

　　1、定期更换水龙头里的垫圈以减少漏水;2、衣服不要放在流水下冲洗;3、将餐具放到滴有洗洁精溶液的盆里浸泡摸洗，冲洗时再拧开水龙头;4、在抽水马桶里放块砖头;5、将用过的水二次利用。<br>

　　二、厨房中的污染<br>

　　厨房中的燃料燃烧会产生二氧化硫、一氧化碳等有害气体和颗粒，这些对人的肺、气管、脑组织等有害;烹饪产生的油烟中含有丙烯醛，对鼻、眼、咽喉黏膜胡较强的刺激作用，容易引起上述器官的病症。<br>
<br>
　　三、推广使用无磷洗衣粉<br>

　　洗衣粉一般都使用磷酸钠作为助洗剂，洗涤后，含磷废水流入江河湖泊，造成水中蓝藻生长迅速，水体流动减缓，鱼类及其他生物因缺氧死亡。而且，洗涤后的衣服容易引起皮肤瘙痒等不良症状。<br>

　　四、有车族的环保小常识<br>

　　按时保养汽车，使尾气污染排放降到最低;一旦车尾开始漏油，马上修理;不开车去人群密集、交通拥堵的闹市区;拒用污染水源、污染环境的方式洗车;长时间堵车时应熄火等待;不从车窗内向外扔垃圾赃物;驾车出游不将垃圾留在野外;拒绝购买和使用高污染车及车用品，提倡使用可降解车用品。<br>

　　五、家电辐射的危害<br>

　　手机、电视、电脑、微波炉等家电在使用过程中都会不同程度地产生不同波长和频率的电磁波，它们无色无味、看不见、摸不着、穿透力强，而且充斥在整个空间，成为一种新的污染源，已经面成为当今危害人类健康的致病源之一。长期处于强辐射下，容易诱导白血病、肌肉肿瘤等疾病的发生。<br>

　　环境保护与日常消费<br>

　　一、野生动物不能随便吃<br>

　　动物容易传染疾病，狂犬病、疯牛病、艾滋病以及非典、禽流感等都是从野生动物开始传播扫。野生动物还含有各种病毒，携带各种寄生虫，吃后易引起出血热、免疫热等病，虫卵会造成中毒或贫血。

　　二、什么是农药残留<br>

　　农药使用后，经风吹日晒大部分逐渐消失，但仍然有部分附在农作物叶片上，被吸收或渗入植物体内。另一部分渗入土壤和水中，被农作物和水生生物吸收，这些被植物、水生生物、鱼类、禽、畜摄入体内的.农药，被称为农药残留。

　　三、绿色消费观<br>

　　随着绿色市场的兴起，一种新型的绿色消费观已经逐步形成。绿色消费观就是在进行个人消费时，将消费利益的保护人类生存环境利益结合起来，认为以牺牲环境为代价换取消费利益是不可取的，从而抵制购买和消费那种在生产和消费过程中产生环境污染的商品.<br>

　　四、分类处理生活垃圾<br>

　　生活垃圾圾一般可分为四大类：可回收垃圾、厨房垃圾、有害垃圾和其他垃圾。可回收垃圾包括纸类、金属类等，回收可减少污染，节约资源;厨房垃圾包括剩饭、骨头、菜叶等废物，经处理后可生产有机肥;有害垃圾包括含汞电池、废日光灯管、过期药品等，需进行安全处理;其他垃圾主要采取焚烧和卫生填埋的方式处理。<br>

　　五、绿色食品<br>

　　绿色食品是遵循可持续发展原则，按照特定的生产方式生产，经专门机构认定，许可使用绿色食品商标标志的，无污染的安全、优质、营养类食品。绿色食品分为a级和aa级。绿色食品必须符合一定的标准，简单的说就是7个字：“安全、营养、无公害”。<br>

　　保护环境 从我做起<br>

　　一、从我做起保护水资源<br>

　　使用节水型水具;杜绝跑冒滴漏;洗漱随手关龙头;一水多用;减少干洗;拒绝化学清洁剂;选用无磷洗衣粉;不向水体排放污水、废液、倾倒垃圾、渣土;不在饮用水源水面上游泳、娱乐、训练;不在水体边洗衣、洗车。<br>

　　二、从我做起防止大气污染<br>

　　关注空气污染指数;乐为“公交族”;出行多骑自行车;“开车族”使用无铅汽油，定期检查汽车尾气;拒食露天烧烤;黄土不露天，绿化美化生活环境;使用无氟电器;少用发胶。<br>
<br>
　　三、从我做起防止噪声扰民<br>

　　公共场所，轻声交谈;居家生活，轻走轻行;音响电视小声;装修想想四邻;开车尽量少鸣笛;不燃放烟花爆竹;夜间、节假日、学生考试期间不制造施工噪声;做生意时不用音响招揽顾客。<br>

　　四、从我做起保护环境<br>

　　节约用水、用电;选用、购买环保产品和绿色产品;买菜、购物使用布袋、菜篮、纸袋;不吃野生保护动物，不吃、少吃口香糖;爱护周围的花草、树木;不乱扔废弃物，垃圾分类投放;不在公共场所吸烟;不在公共场所大声喧哗;参加一次环保志愿者活动;教育孩子，引导周边的人保护环境。<br>
<br>
　　五、爱护花草树木<br>

　　印度加尔各答农业大学德斯教授对一棵树的生态价值进行了计算;一棵50年树龄的树，以累计计算，产生氧气的价值约31200美元;吸收有毒气体、防止大气污染价值约62500美元;增加土壤肥力价值31200美元;涵养水源价值37500美元;为鸟类用其他动物提供繁衍场所价值31250美元;产生蛋白质价值2500美元。除去花、果实和木材价值，总计创值约196000美元。<br>`,
          create_time: '2022.8.12',
          desc: '落实环境责任,完善环保制度。',
          category: '环保知识',
          like: false,
        }
      ],
      searchList: []
    })
  },
  mounted() {
    this.changeIndex(0)
  },
  watch: {
    keyword(newVal, oldVal) {
      if (newVal == '') {
        this.searchList = this.studyCardList
        this.currentIndex = 0
      }
      else {
        this.searchList = []
        for (let item of this.studyCardList) {
          if (item.title.includes(this.keyword)) this.searchList.push(item)
        }
      }
    }
  },
  methods: {
    changeIndex(index) {
      this.currentIndex = index
      if (index == 0) {
        this.searchList = this.studyCardList
        return
      }
      this.searchList = []
      this.studyCardList.forEach(item => {
        if (item.category == this.categoryList[index]) this.searchList.push(item)
      })
    },
    toArticle(article) {
      this.$router.push({
        name: 'article',
        params: {
          article: JSON.stringify(article)
        }
      })
    }
  }
}
</script>

