<template>
  <!--底部导航用户-->
  <div class="user">
    <van-row @click="openModel" v-if="!showForm">
      <van-col span="8">
        <img v-if="username" :src="avatar" />
        <van-icon v-else name="manager-o" style="font-size:30px;margin-left:30px;"></van-icon>
      </van-col>
      <van-col span="14">{{ nickName }}</van-col>
      <van-col span="2">
        <van-icon name="arrow" />
      </van-col>
    </van-row>
    <van-grid :column-num="3" square>
      <van-grid-item @click="clickItem(item)" v-for="(item, index) in gridData" :key="index" :icon="item.icon"
        :text="item.text" />
    </van-grid>
  </div>
</template>

<script>
export default {
  name: "User",
  data() {
    return {
      showForm: false,
      avatar: require('../assets/img/xp.jpg'),
      nickName: '点击登录',
      username: '',
      password: '',
      gridData: [
        { value: 0, text: '留言板', icon: 'comment' },
        { value: 1, text: '诈骗处理', icon: 'cash-on-deliver' },
        { value: 2, text: '法律了解', icon: 'gift-o' },
        { value: 3, text: '我的收藏', icon: 'goods-collect' },
        { value: 4, text: '我的足迹', icon: 'location-o' },
        { value: 5, text: '会员福利', icon: 'manager-o' },
        { value: 6, text: '地址管理', icon: 'setting-o' },
        { value: 7, text: '账号安全', icon: 'warn-o' },
        { value: 8, text: '联系客服', icon: 'service-o' },
        { value: 9, text: '帮助中心', icon: 'question-o' },
        { value: 10, text: '意见反馈', icon: 'service-o' },
        { value: 11, text: '退出登录', icon: 'friends' }
      ]
    }
  },
  created() {
    let userInfo = localStorage.getItem('userInfo')
    if(userInfo){
      this.username = userInfo
      this.nickName = userInfo
    }else{
      this.nickName = '未登录'
    }
  },
  methods: {
    openModel() {
      this.$router.push('/login')
    },
    validator(val) {
      return val.trim().length > 0;
    },
    clickItem(item) {
      if(item.value == 11){
        localStorage.removeItem('userInfo')
        localStorage.removeItem('token')
        this.$toast.success('退出登录成功!')
        this.username = ''
        this.nickName = '点击登录'
        
      }else if(item.value == 0){
        if(localStorage.getItem('userInfo')==''||localStorage.getItem('userInfo')==null){
          this.$toast.fail('请先登录~')
          return
        }
        this.$router.push('/message')
      }else if(item.value == 3){
        if(localStorage.getItem('userInfo')==''||localStorage.getItem('userInfo')==null){
          this.$toast.fail('请先登录~')
          return
        }
        this.$router.push('/collect')
      }else this.$toast.fail('功能暂未开放!');
    }
  }
}
</script>

<style scoped>
#app .user {
  padding-bottom: .5rem;
  /* color: #ff8198; */
  color:brown;
}

.van-row {
  padding: .2rem 4%;
  font-weight: bold;
  color: white;
  /* background-color: var(--color-height-text); */
  background-color: brown;

}

.van-col {
  line-height: 100px;
}

img {
  height: 100px;
  width: 100px;
  /*使图片变圆*/
  border-radius: 50%;
  display: block;
}

.login_modal {
  width: 100%;
  /*元素是否有内容都会撑开*/
  height: 100vh;
  position: absolute;
  /* 属性设置元素的堆叠顺序。拥有更高堆叠顺序的元素总是会处于堆叠顺序较低的元素的前面。*/
  /* 结合position使用*/
  z-index: 999;

  /*设置模态层，专门用于取消*/
}

section {
  /* background: #ff8198; */
  background:brown;
  height: 100vh;
  z-index: 998;
}

.van-form {
  /*绝对定位*/
  position: absolute;
  top: 0;
  left: 0;
  background: white;
  margin: 40% 15%;
  z-index: 999;
  border-radius: 20px;
}
.van-cell{
  border-radius: 20px;
}
.van-button--info{
  background: #ff8198;
  border:1px solid #ff8198;
  color: #fff;
}
</style>
